@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
/* all css */
#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

* {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

input {
  all: unset;
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

/* Auth css */

.auth_body {
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 90px;
}

.auth_icon_twitter {
  margin-bottom: 30px;
  font-size: 50px;
}

.auth_titleText {
  font-size: 40px;
  font-weight: 600;
  color: #04aaff;
  margin-bottom: 40px;
  text-align: center;
  width: 68%;
  line-height: 1.4;
}

.auth_form_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.auth_form {
  background-color: white;
  border-radius: 5px;

  padding: 12px 5px;
  cursor: pointer;
}

.auth_input {
  width: 357px;
  margin-bottom: 20px;
  padding: 20px 5px;
  border-bottom: 2px solid #04aaff;
}

.auth_input:hover,
.auth_input:focus {
  background-color: whitesmoke;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  margin-bottom: 10px;
}

.auth_submit {
  width: 357px;
  text-align: center;
  border-radius: 18px;
  margin-top: 25px;
  margin-bottom: 18px;
  background-color: #04aaff;
  color: white;
  font-weight: 600;
  transition: all 0.2s linear;
}

.auth_switch {
  width: 357px;
  text-align: center;
  border-radius: 18px;
  margin-bottom: 30px;
  border: 1px solid #04aaff;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.2s linear;
}

.auth_submit:hover {
  background-color: #0076e3;
}
.auth_switch:hover {
  background-color: #f1f7ff;
}

.auth_btn_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 357px;
  margin-top: 10px;
}

.auth_socialBtn {
  background-color: white;
  border: 1px solid #04aaff;
  border-radius: 10px;
  padding: 15px;
  font-weight: 600;
  width: 100%;
  cursor: pointer;
  font-size: 15px;
  transition: all 0.1s linear;
}

.github {
  margin-top: 20px;
}
.auth_socialBtn_icon {
  margin-left: 10px;
}

.auth_socialBtn:hover {
  background-color: #04aaff;
  color: white;
}

/* Home css */

.home_body {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Nav css */

.nav_body {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.nav_links {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.nav_link_home {
  padding-right: 30px;
  margin-right: 30px;
  border-right: 1px solid #04aaff;
}

.nav_link_profile {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.nav_link_profile_link {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: grey;
  font-weight: 800;
}

.nav_link_profile_icon {
  margin-right: 10px;
}

.nav_greetings {
  margin-top: 50px;
  font-size: 21px;
  font-weight: 600;
  color: #04aaff;
}

/* NweetFactory css */

.nweet_form_body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nweet_form {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: 1px solid #04aaff;
  border-radius: 20px;
  position: relative;
}

.nweet_form_input {
  padding: 9.3px 10px;
  width: 350px;
}

.nweet_form_submit {
  position: absolute;
  right: 0;
  background-color: #04aaff;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.nweet_form_label {
  margin-top: 30px;
  cursor: pointer;
}

.nweet_form_photo {
  opacity: 0;
}

.nweet_form_label__text {
  margin-right: 10px;
}

.nweet_preview {
  display: flex;
  align-items: center;
  justify-content: center;
}

.nweet_preview__photo {
  height: 300px;
}

.nweet_previewPhoto__btn {
  border: none;
  background-color: white;
}

/* Nweet css */

.nweet_body {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid #c8ecff;
  border-radius: 20px;
  padding: 30px 40px;
}

.nweet_body__sub {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
}

.nweet_body__image {
  width: 300px;
  margin-bottom: 12px;
}

.nweet_body__sub__text {
  font-size: 20px;
}

.nweet_body__sub__btn {
  border: none;
  background-color: white;
  cursor: pointer;
  font-size: 20px;
}
.edit {
  margin-right: 10px;
}

.nweet_edit {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #c8ecff;
  border-radius: 20px;
  padding: 30px 40px;
}

.nweet_edit_text {
  width: 100%;
  font-size: 20px;
}

.nweet_edit_sub {
  width: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nweet_edit_update {
  cursor: pointer;
  border: none;
  background-color: white;
  font-size: 22px;
  margin-right: 5px;
}
.nweet_edit_cancle {
  cursor: pointer;
  font-size: 25px;
}

/* Profile css */

.profile_form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  background-color: #f9f9f9;
  width: 300px;
  padding: 40px;
  margin: 100px;
  border-radius: 20px;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.02),
    0 5px 17.9px rgba(0, 0, 0, 0.01), 0 20px 25px rgba(0, 0, 0, 0.01),
    0 80px 100px rgba(0, 0, 0, 0.05);
}

.profile_icon {
  border: 2px solid #2fb4f9;
  border-radius: 50%;
  padding: 20px;
}

.profile_name {
  margin: 20px 0px;
  text-transform: uppercase;
  font-weight: 900;
}

.profile_update {
  display: flex;
  border: 1px solid #04aaff;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 30px;
}

.profile_form_logOut {
  border: none;
  background: #04aaff;
  width: 230px;
  border-radius: 10px;
  padding: 10px 0px;
  cursor: pointer;
  font-size: 18px;
  color: #f9f9f9;
  font-weight: 800;
  transition: all 0.2s ease-in-out;
}

.profile_form_logOut:hover {
  background-color: #0076e3;
}

.profile_form_update {
  border: none;
  background-color: #f9f9f9;
  cursor: pointer;
}

